.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: var(--body-color);
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
.nav {
  height: calc(var(--header-height) + 1.5rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 1rem;
}
.nav_logo {
  font-weight: var(--font-medium);
}
.nav_toggle {
  color: var(--title-color);
  font-weight: var(--font-medium);
}
.nav_list {
  display: flex;
  column-gap: 2rem;
}
.nav_link {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--small-font-size);
  color: var(--title-color);
  font-weight: var(--font-medium);
  transition: 0.3s;
}
.nav_close,
.nav_icon,
.nav_toggle {
  display: none;
}

/* Active links */
.active-link,
.nav_link:hover {
  color: var(--title-color-dark);
}

/* For medium devices */
@media screen and (max-width: 768px) {
  .header {
    top: initial;
    bottom: 100;
  }
  .nav {
    height: var(--header-height);
  }
  .nav_menu {
    position: fixed;
    bottom: 100%;
    right: 0;
    width: 35%;
    height: 100%;
    background-color: var(--body-color);
    padding: 2rem 1.5rem 4rem;
    box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
    /* border-radius: 0 0 1.5rem 1.5rem; */
    transition: 0.3s;
  }
  /* Show menu */
  .show-menu {
    top: 0%;
  }

  .nav_list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
  }
  .nav_icon {
    font-size: 1.2rem;
  }
  .nav_close {
    position: absolute;
    right: 1.2rem;
    top: 0.5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--title-color);
  }
  .nav_close:hover {
    color: var(--title-color-dark);
  }
  .nav_toggle {
    font-size: 1.1rem;
    cursor: pointer;
  }
  .nav_close,
  .nav_icon,
  .nav_toggle {
    display: block;
  }
}
/* For small devices */
@media screen and (max-width: 350px) {
  .nav_menu {
    padding: 2rem 0.25rem 4rem;
  }
  .nav_list {
    column-gap: 0;
  }
}
