.home_container {
  row-gap: 4rem;
}
.home_content {
  grid-template-columns: 116px repeat(2, 1fr);
  padding-top: 5.5rem;
  column-gap: 2rem;
  align-items: center;
}
.home_social {
  display: grid;
  grid-template-columns: max-content;
  row-gap: 1rem;
}
.home_social_icon {
  font-size: 1.75rem;
  color: var(--title-color);
}
.home_social_icon:hover {
  color: var(--title-color-dark);
}
.home_title {
  font-size: 2.7rem;
  margin-bottom: var(--mb-0-25);
  color: #0987d4;
}
.home_hand {
  width: 38px;
  height: 38px;
  margin-left: 0.6rem;
}

.home_description {
  max-width: 400px;
  margin-bottom: var(--mb-3);
  margin-top: 10px;
}
.home_img {
  background: url(../../assets/profile.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-shadow: inset 0 0 0 9px rgba(191, 171, 171, 0.3);
  order: 1;
  justify-self: center;
  width: 350px;
  height: 350px;
  animation: profile_animate 6s ease-in-out infinite 1s;
}
@keyframes profile_animate {
  0% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
  50% {
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
  }
  100% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
}
.home_scroll {
  margin-left: 9.15rem;
}
.wheel {
  animation: scroll 2s ease infinite;
}
@keyframes scroll {
  0% {
    transform: translateY(0);
  }
  30% {
    transform: translateY(3.75rem);
  }
}
.home_scroll_name {
  color: var(--title-color);
  font-size: var(--font-medium);
  margin-right: var(--mb-0-25);
  margin-left: 2px;
}

.home_scroll_arrow {
  font-size: 1.25rem;
  color: var(--text-color);
}

/* For large devices */
@media screen and (max-width: 992px) {
  .home_data {
    text-align: center;
  }
  .home_title {
    font-size: 2rem;
    text-align: center;
  }
  .home_content {
    grid-template-columns: 100px repeat(2, 1fr);
    column-gap: 1.25rem;
  }
  .home_hand {
    width: 26px;
    height: 26px;
  }
  .home_subtitle {
    padding-top: 0.8rem;
    margin-bottom: var(--mb-1);
  }
  /* .home_subtitle::before {
    width: 42px;
    top: 1.6rem;
  } */
  .home_description {
    max-width: initial;
    margin-bottom: var(--mb-2-5);
  }
  .home_img {
    width: 250px;
    height: 250px;
    box-shadow: inset 0 0 0 8px rgb(255 255 255 / 30%);
  }
  .home_scroll {
    margin-left: 7rem;
  }
}

/* For medium devices */
@media screen and (max-width: 768px) {
  .home_content {
    grid-template-columns: 0.5fr 3fr;
    padding-top: 4.5rem;
  }
  .home_img {
    order: initial;
    justify-self: initial;
  }
  .home_data {
    grid-column: 1/3;
  }
  .home_img {
    box-shadow: inset 0 0 0 6px rgb(255 255 255 / 30%);
    width: 220px;
    height: 220px;
  }
  .home_scroll {
    display: none;
  }
}

/* For small devices */
@media screen and (max-width: 350px) {
  .home_title {
    font-size: 1.5rem;
  }
  .home_subtitle {
    padding-left: 0rem;
    font-size: 0.9rem;
  }
  .home_img {
    box-shadow: inset 0 0 0 6px rgb(255 255 255 / 30%);
    width: 180px;
    height: 180px;
  }
  .home_hand {
    width: 22px;
    height: 22px;
  }
}
